export default {
	data() {
		return {
			venueList: []
		}
	},
	created() {
		this.$http.get({
			url: '/v1/rest/venue/getVenueInfo',
			data: {
				name: ''
			}
		}).then(resp => {
			console.log(resp)
			if(resp.status == 200) {
				this.venueList = resp.data
			}
		})
	},
	methods: {
		onChange(v) {
			this.$http.get({
				url: '/v1/rest/venue/getVenueInfo',
				data: {
					name: v.detail
				}
			}).then(resp => {
				console.log(resp)
				if(resp.status == 200) {
					this.venueList = resp.data
				}
			})
		},
		selectVenue(item) {
			const selected = {
				name: item.name,
				id: item.id
			}
			uni.setStorage({
				key: 'venue',
				data: selected
			})
			uni.navigateBack()
		}
	}
}
